@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
section.homepage{
    background-color: #101426;
    height:100%;
    padding-bottom: 30px;
}

section.homepage .header{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 80px;
}

section.homepage .header h1{
    --text-opacity: 1;
    /* color: #fff; */
    color: rgba(255,255,255,var(--text-opacity));
    /* font-weight: 800; */
    font-weight: 600;
    text-transform: uppercase;
    font-family: "DM Sans",sans-serif!important;
    font-size: inherit;
}

section.homepage .text{
    display: flex;
    /* align-items: center; */
    /* justify-content: center; */
    /* justify-items: center; */
    text-align: center;
    margin: 20px 50px;
}

section.homepage .text p{
    color: #8f9bb3;
    line-height: 1.5em;
    opacity: 1;
    transform: translateZ(0) scaleX(1) rotateX( 
0deg
 ) rotateY( 
0deg
 ) rotate( 
0deg
 ) skew( 
0deg
 , 
0deg
 );
    transform-style: preserve-3d;
    font-size: 18px;
}

div.grid{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    /* align-items: center; */
    justify-items: center;
    grid-gap: 20px 0px;
    margin-top: 60px;
}

div.small-box{
    background-color: #fff;
    box-shadow: 0 1px 2px 0 rgb(0 0 0 / 5%);
    border-radius: .25rem;
    color:#2d3748;
    border: 0 solid #101426;
    width: 90%;
    height: 80px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    cursor: pointer;
}

div.small-box p{
    font-weight: 600;
}

div.small-box img{
width:40px;
height:auto;
}
@media screen and (max-width: 425px){
    div.grid{
       
        grid-template-columns: repeat(1, 1fr);
       
    }
    section.homepage .text{
       
        margin: 15px 30px;
    }

    section.homepage .text p{
        color: #8f9bb3;
        line-height: 1.3em;
        opacity: 1;
        font-size: 18px;
    }
}

@media screen and (max-width: 320px){
    
    section.homepage .text{
       
        margin: 10px 30px;
    }

    section.homepage .text p{
        color: #8f9bb3;
        line-height: 1.3em;
        opacity: 1;
        font-size: 15px;
    }
}

*{
  padding:0;
  margin: 0;
  box-sizing: border-box;
  /* transition:all 0.2s ease-in-out; */
  /* font-family: 'Roboto', sans-serif; */
  font-family: 'Montserrat', sans-serif;

}
*::selection{
  background:#9E9E9E ;
}

body{
  background: #101426;
  color:white;
}
section.popup{
    position: fixed;
    background: #c5ccd8b5;
    /* height: 100%; */
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center
}

section.popup>div{
    background: #ffffff;
    height: auto;
    width: 50%;
    border-radius: 15px;
    color:black;
    padding: 20px;

}

span svg{
    width: 1.5rem;
    height: 1.5rem;
    color: #c2b6b6;
    
}

div.form-header{
    display: flex;
    align-items: center

}
div.form-header img{
    width: auto;
    height: 50px;
    margin-right: 15px;
    
}

div.form-header p{
    font-weight: 500;
    font-size: 1.125rem;
    line-height: 1.5rem;
    color: rgba(26,32,44,var(--text-opacity));
}

p.error-success-msg{
    text-align: center;
    color:#c73d3d;
    font-weight:300
}
div.form{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 19px;
}

form div{
    display: flex;
    flex-direction: column;
    /* align-items: center */
    margin-bottom: 15px;


}

form div label{
    margin-bottom: 5px;
}

form div input{
    height: 30px;
    padding: 20px 0px 20px 40px;
    border-style: solid;
    border-radius: 6px;
    color: black;
}

span.input{
    position: absolute;
    transform: translate(50%, 120%);
}

div.recovery{
    margin-bottom: 5px;
}
form{
    width:100%;
}
form div textarea{
    max-width: 100%;
    min-width: 100%;
    height: 120px;
    border-style: solid;
    border-radius: 6px;
    padding: 20px 20px 20px 40px;
}

span.textarea{
    position: absolute;
    transform: translate(50%, 240%);
}

form h6{
    font-size: 12px;
    font-weight: 300;
}

form h6 i{
    margin-right: 5px; 
}

div.proceed-button{
    background:#3182ce;
    border-radius: 6px;
    flex-direction: row;
    align-items: center;
    /* justify-items: center; */
    justify-content: center;
    padding: 10px;
    margin-top: 13px;
    color:white;
    text-transform:uppercase;
}
div.proceed-button  svg{
    color:white;
    width: 1.2rem;
    height: 1.5rem;
    margin-left: 10px;
}

div.disabled{
    background: #5e83a0;
    cursor: none!important;
}
div.proceed-button:hover{
    cursor: pointer;
    background: #5e83a0;
    transition: all 0.5s ease-in;
}    

div.cancel{
    display: flex;
    flex-direction: row-reverse;
    width: 100%;
    margin-top: 10px;
}
div.cancel p:hover{
    cursor: pointer;
    background: #c73d3d;
    transition: all 0.5s ease-in;
}

div.disabled-cancel p{
    background: #c73d3d;
    cursor: none!important;
}

div.cancel p{
    background-color: #e53e3e;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    padding-top: .75rem;
    padding-bottom: .75rem;
    border-radius: 6px;
    color: white;
}

div.success-message-wrapper{
    height: 50%;
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
}

div.success-message{
    display:flex;
    flex-direction:column;
    text-align: center;
    justify-content: center;
    align-items: center;
   
    /* height:200px; */
  
}

div.success-message img{
    height: 100px;
    width: auto;
}

div.success-message h1{
    font-weight: 900;
    margin: 20px 0;
    color: #ba6d09;
}

div.success-message h4{
    margin-bottom: 20px;
    font-weight: 300;
    color: #79582d;
}

div.success-message p{
    background: #0c0c0c;
    color: burlywood;
    padding: 10px 10px;
    border-radius: 6px;
    cursor:pointer;
}

@media screen and (max-width: 1024px){
    section.popup>div{
        width:45%;
    
    }
}

@media screen and (max-width: 768px){
    section.popup>div{
        width:60%;
    
    }
}

@media screen and (max-width: 425px){
    section.popup>div{
        width:90%;
    
    }

    

    p.error-success-msg{
        font-size: 14px;
    }
}


@media screen and (max-width: 375px){
    div.proceed-button{
        
        padding: 5px;
        
    }

    div.proceed-button p{
        
        font-size: 14px;
        
    }

    div.cancel p{
       
        padding-left: 1.2rem;
        padding-right: 1.2rem;
        padding-top: .75rem;
        padding-bottom: .75rem;
        
    }
}

@media screen and (max-width: 320px){
    section.popup>div{
        width:100%;
        border-radius: 0px;
        height: 100%;
    }

    

    
}
